<template>
  <div id="TodoApp" class="text-center">
    <h1>Ma Todo List</h1>
    <form class="form bg-primary p-3 w-75 m-auto mb-5 rounded">
      <label for="task" class="text-white p-1">Tâches</label>
      <input v-model="task" type="text" class="m-1 p-1"  placeholder="Ajouter une tâche" />
      <button @click="submitTask" type="button" class="m-1 btn btn-warning">Ajouter</button>
    </form>

    <div id="todo" class="w-75 m-auto">

      <table class="table table-dark">
        <thead class="bg-primary">
          <th>Task</th>
          <th>Status</th>
          <th>#</th>
          <th>#</th>
        </thead>
        <tbody>
          <tr v-for="(task, index) in tasks" :key="index">
            <td :class="{'Terminer' : task.status === 'Terminer'}">{{capitalizeFirstLetter(task.name)}}</td>
            <td @click="changeState(index)" :class="{'En_cours' : task.status === 'En cours', 'En_attente' : task.status === 'En attente', 'Terminer_status' : task.status === 'Terminer'}" ><span class="pointer">{{task.status}}</span></td>
            <td><i class="fas fa-pen pointer" @click="editTask(index)"></i></td>
            <td><i class="fas fa-trash pointer" @click="deleteTask(index)"></i></td>
          </tr>
        </tbody>
      </table>

    </div>

  </div>
</template>

<script>
export default {
  name: 'TodoApp',
  data(){
    return{
      task:"",
      editedTask:null,
      TodoState:["En attente","En cours", "Terminer"],
      tasks:[],
      storage: false
    }
  },
  created: function(){
      this.tasks = JSON.parse(localStorage.getItem("todo") || [])
  },
  methods:{
    submitTask(){
      if(this.task.length === 0) return;

      if(this.editedTask === null){
        this.tasks.push({
            name:this.task,
            status: this.TodoState[0],
          })

        localStorage.setItem("todo", JSON.stringify(this.tasks));
      }
      else{
        this.tasks[this.editedTask].name = this.task;
        this.editedTask = null;
      }
      this.task = "";

    },
   
    deleteTask(index){
      this.tasks.splice(index,1);
      localStorage.setItem("todo", JSON.stringify(this.tasks))
    },
    editTask(index){
      this.task = this.tasks[index].name;
      this.editedTask = index;
    },
    changeState(index){
      let newIndex = this.TodoState.indexOf(this.tasks[index].status);
      if(++newIndex > 2) newIndex = 0;
      this.tasks[index].status = this.TodoState[newIndex];
    },
    capitalizeFirstLetter(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

#todo{
  margin-top: 10px;
}

.pointer{
  cursor: pointer;
}

.En_cours{
  color: yellowgreen;
}

.En_attente{
  color: yellow;
}

.Terminer_status{
  color: red;
}

.Terminer{
  text-decoration: line-through;
}

</style>
